.projects{
    background: #1A1A1A ;
    width: 100%;
    
    h1{
        padding-top: 15vh;
        font-size: 50px;
        color: white;
        font-family: 'Raleway', sans-serif;
    }
    p{
        color: white;
        font-size:24px;
        color: lightgray;
    }

    .area{
        display:flex;
        padding-top: 10vh;
        padding-bottom: 10vh;
        justify-content: center;
        width: 100%;
        .container{
            display: flex;
        align-items:center;
        justify-content:center;
        flex-wrap: wrap;
            width: 60%;
            .item {
                width: 300px;
                text-align: left;
                height: 330px;
                
                border-radius: 20px;
                border: 2px solid  #CEC5FC;
                margin: 10px 20px;
                padding: 2vh;
                transition: all 1s ease;
                @media (max-width: 800px) {
                    width: 200px;
                    height: 220px;
                    margin: 1px;

                    .projectTitle{
                        height: 60px;
                    }
                }
                @media (max-width: 1200px) and (min-width: 801px) {
                    width: 250px;
                    height: 260px;
                    margin: 2px;
                    .projectTitle{
                        height: 60px;
                    }
                }
               
    
                .projectTitle{
                    height: 80px;
                    h3 {
                    
                        font-size: 18px;
                        padding-left: 1vh;
                        margin-top: 1vh;
                        padding-bottom: 1vh;
                        color: white;
                        width: 80%;
                        @media (max-width: 800px) {
                            font-size: 16px;
                        }
                        
                    }
                }
    
                img {
                    width: 100%;
                    height: 200px;
                    object-fit:cover;
                    z-index: 1;
                    border-radius: 20px;
                    @media (max-width: 800px) {
                        width: 100%;
                        height: 90px;
                    }
    
                    @media (max-width: 1200px) and (min-width: 801px) {
                        width: 100%;
                        height: 135px;
                    }
                }
                a {
                    margin-left: 1vh;
                    
                    padding: 2vh;
                    background-color: #C5D7FC;
                    border-radius: 5px;
                    text-decoration: none;
                    font-family: 'Raleway', sans-serif; 
                    color: black;
                    @media (max-width: 800px) {
                        padding:1vh; 
                    }
                }
                a:hover {
                    font-weight: bold;
                    background-color: #FCC5F3;
                }
    
                
            }
        }
    }
}